.todoItem {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 6px;
    margin-bottom: 6px;
    padding-left: 10px;
    padding-right: 10px;
    background-color: cyan;
    color: black;
    width: 400px;
    min-width: 300px;
}

.TodoItem-p--complete {
    text-decoration: line-through;
  }
  
  .Icon {
    cursor: pointer;
    font-size: 20px;
    font-weight: bold;
  }
  
  .Icon-check--active {
    color: #4caf50;
  }
  
  .Icon-delete:hover {
    color: red;
  }