.LoadingTodo-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 6px;
    margin-bottom: 6px;
    padding-left: 10px;
    padding-right: 10px;
    color: black;
    width: 400px;
    min-width: 300px;
}

.LoadingTodo-completeIcon, .LoadingTodo-deleteIcon {
    cursor: pointer;
    font-size: 20px;
    font-weight: bold;
}

.LoadingTodo-completeIcon, .LoadingTodo-deleteIcon, .LoadingTodo-container {
    background: linear-gradient(90deg, rgba(250,250,250,1),rgb(188, 255, 255));
    background-size: 400% 400%;
    animation: loadingAnimation 2s ease-in-out infinite;
}

@keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}