.todoForm {
    min-height: 210px;
    min-width: 330px;
    max-width: 330px;
    border: 1px solid white;
    border-radius: 6px;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.2);
}

label {
    color: black;
    font-size: 20px;
    margin-bottom: 10px;
}

.todoArea {
    width: 300px;
    height: 100px;
    resize: none;
}

.buttons {
    margin-top: 10px;
    display: flex;
    justify-content: space-around;
    width: 300px;
}

.buttons button {
    width: 120px;
    height: 40px;
    border-radius: 4px;
    background-color: cyan;
}